import React, { Component } from "react";
import { Link } from "react-router-dom";
import Logo from '../../../Image/logo-dark.png'
export default class Navigation extends Component {
  Click = () => {
    this.setState({ menu: "translateX(0px)" });
  };
  CClick = () => {
    this.setState({ menu: "translateX(-1500px)" });
  };
  onServiceClick =() =>{
    this.setState({service_open:!this.state.service_open,about_open:false})
  }
  onAboutClick =() =>{
    this.setState({about_open:!this.state.about_open,service_open:false})
  }
  constructor() {
    super();
    this.state = {
      menu: "translateX(-900px)",
      transition: ".5s",
      service_open:false,
      about_open:false
    };
  }
  render() {
    const { transition ,service_open,menu,about_open} = this.state;
    const style_service = service_open ?({ display: "block"}):({ display: "none"})
    const style_about = about_open ?({ display: "block"}):({ display: "none"})
    const style = { transform: menu, transition: transition };
    return (
        <div className='nav-mob'>
<div className='logo-bars'>
<div className='bars' onClick={this.Click}>
              <div className='bar'></div>
              <div className='bar'></div>
              <div className='bar'></div>
          </div>
<Link className='logo-link' to='/'>
              <img className='navigation-logo' src={Logo} alt='IBTS Logo' />
          </Link> 
</div>
          <nav style={style}>
          <i onClick={this.CClick} class="far fa-times-circle"></i>
              <ul>
                  <li onClick={this.CClick} ><Link className='menu-link' to='/'>Home</Link></li>
                  <li onClick={this.onServiceClick} ><Link className='menu-link' to=''>Services</Link></li>
                  <div className='sub-menu' style={style_service}>
                  <li onClick={this.CClick} ><Link className='menu-link' to=''>Web Development</Link></li>
                  <li onClick={this.CClick} ><Link className='menu-link' to=''>Artificial intelligence</Link></li>
                  <li onClick={this.CClick} ><Link className='menu-link' to=''>Business intelligence</Link></li>
                  <li onClick={this.CClick} ><Link className='menu-link' to=''>Graphic Design</Link></li>
                  </div>
                  <li onClick={this.CClick} ><Link className='menu-link' to='/portfolio'>Portfolio</Link></li>
                  <li onClick={this.onAboutClick} ><Link className='menu-link' to=''>About us</Link></li>
                  <div className='sub-menu' style={style_about}>

<li onClick={this.CClick} ><Link className='menu-link' to='/about'>About IBTSBD</Link></li>
<li onClick={this.CClick} ><Link className='menu-link' to='/privacy-policy'>Privacy Policy</Link></li>
<li onClick={this.CClick} ><Link className='menu-link' to='/terms-condition'>Terms and Condition</Link></li>
</div>
                  <li onClick={this.CClick} ><Link className='menu-link' to='/blog'>Blog</Link></li>
                  <li onClick={this.CClick} ><Link className='menu-link' to='/contact'>Contact</Link></li>
              </ul>
          </nav> 
        </div>
    );
  }
}

